import React from 'react';

import styles from './index';

export default props => {
  const { data, height, maxVal, scale, handleOnClick } = props;
  const handleMouseenter = e => (e.target.style.opacity = 1);

  const handleMouseout = e => (e.target.style.opacity = 0.7);
  const { x, y } = scale;
  const bars = data.map((d, i) => {
    const delay = i * 0.3;
    return (
      <g key={i}>
        <rect
          className={styles.rect}
          onMouseEnter={handleMouseenter}
          onMouseDown={handleMouseenter}
          onClick={e => handleOnClick(e, i)}
          onMouseOut={handleMouseout}
          fill={`#42${i}6f4`}
          style={{
            opacity: '0.7',
            cursor: 'pointer',
            '--y': `${y(d.value)}`,
            '--delay': `${delay}s`,
            '--maxVal': `${maxVal * 9}`
          }}
          width={x.bandwidth()}
          height={height - y(d.value)}
          x={x(d.name)}
          y={y(d.value)}
        >
          <style jsx="true">{`
            @keyframes rectAnimate {
              0% {
                y: var(--maxVal);
              }
              100% {
                y: var(--y);
              }
            }
            rect {
              animation-name: rectAnimate;
              transition: 1s;
              animation-duration: var(--delay);
              // animation-delay: var(--delay);
              animation-timing-function: cubic-bezier(1, -1.07, 0, 1.42);
              animation-fill-mode: forwards;
              z-index: 10;
            }
          `}</style>
        </rect>
        <text
          id="Bar-text"
          x={x(d.name)}
          // y={y(d.value)}
          y={height}
          dx="24"
          dy="-4"
          textAnchor="middle"
          fontSize="12"
          pointerEvents="none"
          tabIndex="1"
        >
          {d.value}
          <style jsx="true">{`
            #Bar-text {
              z-index: 5;
              user-select: none;
              fill: white;
            }
          `}</style>
        </text>
      </g>
    );
  });

  return <React.Fragment>{bars}</React.Fragment>;
};



